.cohort-form {
  background: white;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.cohort-form > .header {
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
  z-index: 1;
}

.cohort-form > .content {
  padding: 12px;
}

.cohort-form > .content > * {
  margin-bottom: 16px;
}
